.board-nav-filter {
  outline: none;
  font-size: em(16px);
  padding: 0.5em 0.5em;
  line-height: 19px;
  background-color: $trnsprnt;
  border: none;
  border-radius: 4px;
  &:focus {
    width: 250px;
    background-color: #fff;
  }
  &::placeholder {
    color: $darktxt;
  }
}

span {
  vertical-align: middle;
  margin-right: 5px;
}

span.MuiIconButton-label {
  margin-right: 0px;
}
