
.card-edit-container {
    display: flex;
    flex-direction: row;

    .card-edit-right {
        margin: 0rem 0.5rem;
      
        button {
            background-color: black;
            opacity: 0.7;
            color: white;
            margin-bottom: 0.25rem;
            span {
                margin-left: 0.25rem;
            }
        }
    }
    .card-edit-left {
        .card-preview:hover {
            background-color: white;
        }
    .card-preview {
        margin: 0px;

    }
    .save-btn {
        margin: 1rem 0px;
        padding: 6px 24px;
        font-size: 14px;
        color:#fff;
        background-color: $bluebtn;
    }
    }
}

.card-preview-edit {
    background-color: #fff;
    min-height: 58px;
    box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
    margin-bottom: 8px;
    padding: 6px 8px 2px;

}

.card-preview-title-edit {
    width: 100%;
    min-height: 3rem;
    resize: none;
    font-size: 18px;
    border: none;
    outline: 0px;

    &:focus{
        border: none;
    }
}
.card-preview-edit:hover {
    background-color: white;
}

.card-preview-edit-actions-container{

    display: flex;
    flex-direction:column;
    align-items: flex-start;

    span:hover{
        color: $bluebtn;
    }
  
}