.description-txt {
  flex-grow: 1;
}

.modal-description,
.description-text-box {
  margin: 15px 3px 4px -6px;
  padding: 8px 0;
  cursor: pointer;
  pre {
    background-color: rgba(9, 30, 66, 0.04);
    border: none;
    border-radius: 3px;
    min-height: 40px;
    padding: 8px 12px;
    text-decoration: none;

    
    // &:active {
    //     cursor: pointer;
    //     font-size: rem(12px);
    //     border: none;
    //     outline: none;
    //     height: 108px;
    // }
    &pre:focus {
      cursor: pointer;
      font-size: rem(12px);
      border: none;
      outline: none;
      height: 108px;
    }
  }
}

