.card-details-cover-color,
.card-details-cover-image {
  width: 100%;
  height: 100px;
  justify-content: flex-end;
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.card-details-cover-modal-close {
  height: 40px;
  width: 40px;
  bottom: 22px;
  right: 3px;
}

.modal-close {
  height: 40px;
  width: 40px;
  bottom: 20px;
  left: 15px;
}

.card-details-modal-container {
  padding: 20px;
  margin: auto;
  overflow-x: hidden;
}

#modal-close {
  padding: 8px;
  margin: 0;
  align-self: center;
}

.card-modal-title {
  margin-bottom: 2px;
}
